import React, { useEffect, useState } from 'react'
// import { SwapPoolTabs } from '../../components/NavigationTabs'
import { Wrapper } from '../../components/swap/styleds'
import { AutoColumn } from '../../components/Column'
import { AutoRow } from '../../components/Row'
import AppBody from '../AppBody'
import styled from 'styled-components'
import Logo from '../../assets/images/sticker.webp'
//import CountUp from 'react-countup';
//import { StyledInternalLink } from '../../theme'

export const Item = styled.div`
  display: flex
  justify-content: center
  padding: .5rem
`

const Grid = styled.div`
  display: grid
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: 30px 30px
  grid-gap: 5px
`

interface Prices {
  mswap: {
    price: number
    mcap: number
    pic: string
  }
  rip: {
    price: number
    mcap: number
    pic: string
  }
  moon: {
    price: number
    mcap: number
    pic: string
  }
  kdoge: {
    price: number
    mcap: number
    pic: string
  }
  kmoon: {
    price: number
    mcap: number
    pic: string
  }
  riverstream: {
    price: number
    mcap: number
    pic: string
  }
}

export default function Rugs() {
  const FAST_INTERVAL = 10000
  const [data, setData] = useState<Prices | null>()
  const [fast, setFast] = useState(0)
  console.log(fast);
  

  useEffect(() => {
    async function getData() {
      const response = await fetch('https://api.moonfarm.in/api/prices/')
      const responseData: Prices = await response.json()
      // console.log(responseData)
      setData(responseData)
      console.log(data)
    }
    getData()
    const interval = setInterval(async () => {
      setFast((prev) => prev + 1)
      getData()
    }, FAST_INTERVAL)
    return () => clearInterval(interval)
  }, [setData])

  const renderTable = () => {
    console.log(data)
    if (data != null) {
      console.log(data.mswap)
      return (
        <>
          <Grid style={{alignItems:"center"}}>
            <Item><img style={{alignSelf:"center"}}src="https://raw.githubusercontent.com/moonfarmin/moonfarm-contracts-v2/master/rugs/saga.png" height="34px"></img><p>SAGA</p></Item>
            
          </Grid>
          <Item><img style={{alignSelf:"center"}} height=""src={Logo}></img></Item>
        </>
      )
    } else return <AutoRow justify="start"> asd</AutoRow>
  }

  return (
    <>
      <AutoColumn justify="center">
        <h1>Rugs</h1>
      </AutoColumn>
      <AppBody>
        <Wrapper id="swap-page">{renderTable()}</Wrapper>
        <Grid >
            <Item>Report any new rugs &nbsp; <a style={{color:"red",textDecoration:"none"}}href="https://t.me/kabanashi0x"target="_blank"> here</a></Item>
          </Grid>
      </AppBody>
    </>
  )
}

