import React, { useEffect, useState } from 'react'
// import { SwapPoolTabs } from '../../components/NavigationTabs'
import { Wrapper } from '../../components/swap/styleds'
import { AutoColumn } from '../../components/Column'
import { AutoRow } from '../../components/Row'
import AppBody from '../AppBody'
import styled from 'styled-components'
import CountUp from 'react-countup';
import { StyledInternalLink } from '../../theme'

export const Item = styled.div`
  display: flex
  justify-content: center
  padding: .5rem
`

const Grid = styled.div`
  display: grid
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 30px 30px
  grid-gap: 5px
`

interface Prices {
  mswap: {
    price: number
    mcap: number
    pic: string
  }
  rip: {
    price: number
    mcap: number
    pic: string
  }
  moon: {
    price: number
    mcap: number
    pic: string
  }
  kdoge: {
    price: number
    mcap: number
    pic: string
  }
  kmoon: {
    price: number
    mcap: number
    pic: string
  }
  riverstream: {
    price: number
    mcap: number
    pic: string
  }
  freeriver: {
    price: number
    mcap: number
    pic: string
  }

}

export default function Stats() {
  const FAST_INTERVAL = 10000
  const [data, setData] = useState<Prices | null>()
  const [fast, setFast] = useState(0)
  console.log(fast);
  

  useEffect(() => {
    async function getData() {
      const response = await fetch('https://api.moonfarm.in/api/prices/')
      const responseData: Prices = await response.json()
      // console.log(responseData)
      setData(responseData)
      console.log(data)
    }
    getData()
    const interval = setInterval(async () => {
      setFast((prev) => prev + 1)
      getData()
    }, FAST_INTERVAL)
    return () => clearInterval(interval)
  }, [setData])

  const renderTable = () => {
    console.log(data)
    if (data != null) {
      console.log(data.mswap)
      return (
        <>
          <Grid style={{fontWeight:"bold"}}>
            <Item>Token</Item>
            <Item>Price</Item>
            <Item>Market Cap</Item>
          </Grid>

          <Grid style={{alignItems:"center"}}>
            <Item><img style={{alignSelf:"center"}}src={data.mswap.pic} height="24px"></img><StyledInternalLink to="/swap?outputCurrency=0xB3FB48bF090bEDFF4f6F93FFb40221742E107db7">mSWAP</StyledInternalLink></Item>
            <Item><CountUp prefix="$"duration={1} decimals={2} end={Number(data.mswap.price)} /></Item>
            <Item><CountUp prefix="$"duration={1}separator="," decimals={2} end={Number(data.mswap.mcap)} /></Item>
          </Grid>

          <Grid style={{alignItems:"center"}}>
          <Item><img style={{alignSelf:"center"}}src={data.moon.pic} height="24px"></img><StyledInternalLink to="/swap?outputCurrency=0xB497c3E9D27Ba6b1fea9F1b941d8C79E66cfC9d6">MOON</StyledInternalLink></Item>
          <Item><CountUp prefix="$"duration={1} decimals={2} end={Number(data.moon.price)} /></Item>
          <Item><CountUp prefix="$"duration={1}separator="," decimals={2} end={Number(data.moon.mcap)} /></Item>
          </Grid>

          <Grid style={{alignItems:"center"}}>
          <Item><img style={{alignSelf:"center"}}src={data.rip.pic} height="24px"></img><p>RIB</p></Item>
          <Item><CountUp prefix="$"duration={1} decimals={2} end={Number(data.rip.price)} /></Item>
          <Item><CountUp prefix="$"duration={1}separator="," decimals={2} end={Number(data.rip.mcap)} /></Item>
          </Grid>

          
          <Grid style={{alignItems:"center"}}>
          <Item><img style={{alignSelf:"center"}}src={data.kmoon.pic} height="24px"></img><StyledInternalLink to="/swap?outputCurrency=0x480CD4Fa911eeEFf93cB11135C97237455617862">KMOON</StyledInternalLink></Item>
          <Item><CountUp prefix="$"duration={1} decimals={8} end={Number(data.kmoon.price)} /></Item>
          <Item><CountUp prefix="$"duration={1}separator="," decimals={2} end={Number(data.kmoon.mcap)} /></Item>
          </Grid>

          <Grid style={{alignItems:"center"}}>
          <Item><img style={{alignSelf:"center"}}src={data.kdoge.pic} height="24px"></img><StyledInternalLink to="/swap?outputCurrency=0xd7D4a2D99314C8D749050FEfA33c5566EB2C5681">KDOGE</StyledInternalLink></Item>
          <Item><CountUp prefix="$"duration={1} decimals={8} end={Number(data.kdoge.price)} /></Item>
          <Item><CountUp prefix="$"duration={1}  separator="," decimals={2} end={Number(data.kdoge.mcap)} /></Item>
          </Grid>

          <Grid style={{alignItems:"center"}}>
          <Item><img style={{alignSelf:"center"}}src={data.riverstream.pic} height="24px"></img><p> RS</p></Item>
          <Item><CountUp prefix="$"duration={1} decimals={8} end={Number(data.riverstream.price)} /></Item>
          <Item><CountUp prefix="$"duration={1}  separator="," decimals={2} end={Number(data.riverstream.mcap)} /></Item>
          </Grid>

          <Grid style={{alignItems:"center"}}>
          <Item><img style={{alignSelf:"center"}}src={data.freeriver.pic} height="24px"></img><p> FREE</p></Item>
          <Item><CountUp prefix="$"duration={1} decimals={8} end={Number(data.freeriver.price)} /></Item>
          <Item><CountUp prefix="$"duration={1}  separator="," decimals={2} end={Number(data.freeriver.mcap)} /></Item>
          </Grid>
        </>
      )
    } else return <AutoRow justify="start"> asd</AutoRow>
  }

  return (
    <>
      <AutoColumn justify="center">
        <h1>Prices</h1>
      </AutoColumn>
      <AppBody>
        <Wrapper id="swap-page">{renderTable()}</Wrapper>
        <Item>Want your token listed? click&nbsp; <a style={{color:"red",textDecoration:"none"}}href="https://t.me/kabanashi0x"target="_blank"> here</a></Item>
      </AppBody>
    </>
  )
}

